  align-items: center;
}
.items-start {
  align-items: flex-start;
}
.items-end {
  align-items: flex-end;
}
.items-baseline {
  align-items: baseline;
}

// --------------------------------

// Order

// --------------------------------

.order-1 {
  order: 1;
}
.order-2 {
  order: 2;
}
.order-3 {
  order: 3;
}

// --------------------------------

// Aspect Ratio

// --------------------------------

[class^="aspect-ratio"],
[class*=" aspect-ratio"] {
  --aspect-ratio: calc(16 / 9);
  position: relative;
  height: 0;
  padding-bottom: calc(100% / (var(--aspect-ratio)));

  > * {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    &:not(iframe) {
      object-fit: cover;
    }
  }
}

.aspect-ratio-16\:9 {
  --aspect-ratio: calc(16 / 9);
}
.aspect-ratio-3\:2 {
  --aspect-ratio: calc(3 / 2);
}
.aspect-ratio-4\:3 {
  --aspect-ratio: calc(4 / 3);
}
.aspect-ratio-5\:4 {
  --aspect-ratio: calc(5 / 4);
}
.aspect-ratio-1\:1 {
  --aspect-ratio: calc(1 / 1);
}
.aspect-ratio-4\:5 {
  --aspect-ratio: calc(4 / 5);
}
.aspect-ratio-3\:4 {
  --aspect-ratio: calc(3 / 4);
}
.aspect-ratio-2\:3 {
  --aspect-ratio: calc(2 / 3);
}
.aspect-ratio-9\:16 {
  --aspect-ratio: calc(9 / 16);
}

