  text-shadow: 0 1px 1px rgba(#000, 0.15);
}
.text-shadow-sm {
  text-shadow: 0 1px 2px rgba(#000, 0.25);
}
.text-shadow-md {
  text-shadow: 0 1px 2px rgba(#000, 0.1), 0 2px 4px rgba(#000, 0.2);
}
.text-shadow-lg {
  text-shadow: 0 1px 4px rgba(#000, 0.1), 0 2px 8px rgba(#000, 0.15),
    0 4px 16px rgba(#000, 0.2);
}
.text-shadow-xl {
  text-shadow: 0 1px 4px rgba(#000, 0.1), 0 2px 8px rgba(#000, 0.15),
    0 4px 16px rgba(#000, 0.2), 0 6px 24px rgba(#000, 0.25);
}
.text-shadow-none {
  text-shadow: none;
}

