[class^="rotate"],
[class*=" rotate"],
[class^="-translate"],
[class*=" -translate"],
[class^="translate"],
[class*=" translate"],
[class^="-scale"],
[class*=" -scale"],
[class^="scale"],
[class*=" scale"],
[class^="-skew"],
[class*=" -skew"] [class^="skew"],
